import React, { useEffect, useState } from "react";
import PayTitle from "../component/PayTitle";
import { useHistory } from "react-router";
import Header from "../component/Header";
import axios from "axios";
import '../component/Container.css';
import '../component/Footer.css';
import PayArea from "../component/PayArea";
import PayInfo from "../component/PayInfo";
import PayUser from "../component/PayUser";
import PayDeliver from "../component/PayDeliver";
import { useLocation } from 'react-router';

const PayPage = (props) => {
    const history= useHistory();
    const [payCard, setPayCard] = useState("");
    var voices = window.speechSynthesis.getVoices();
    const location = useLocation();
    const [cardForm, setCardForm] = useState({
        card: "",
        pwd: ""
    })
    
    const [msg, setMsg] = useState("");
    const [isSpeakDone, setIsSpeakDone] = useState(false); //   
    const [speakOnce, setSpeakOnce] = useState(false);
    const [nextField, setNextField] = useState("id"); //     
    const [nextAction, setNextAction] = useState("");
    const [products, setProducts] = useState([]);
    const [idx, setIdx] = useState(0); // 
    const [title, setTitle] = useState(); // 
    const [price, setPrice] = useState(); //  
    const [amount, setAmount] = useState(); // 
    const [totalPrice, setTotalPrice] = useState();

    // const [num, setNum] = useState(0);
    // setNum(parseInt(info.priceList[0]) + parseInt(info.priceList[1]));

    let tempTitle = [];
    let temp = [];
    let temp2 = [];
    let productPrice = ""

    useEffect(()=>{
        const fetchData = async () => {
            try{
                const response1 = await axios.get('https://springservertest.herokuapp.com/payment/showpayment', {
                    params: {
                        customer: props.loginID
                    }});
                setProducts(response1.data);
                console.log(""+typeof response1.data);
                const pay= response1.data;
                console.log("  "+JSON.stringify(response1.data))
                console.log(" api"+pay)
                const result = pay.map((product, index) => (product.price));
                setPrice(result);
                const result2 = pay.map((product, index) => (product.title));
                setTitle(result2);
                const result3 = pay.map((product, index) => (product.num));
                setAmount(result3);
                result.entries();
                let sum = 0;
                for(let i = 0; i <=10; i++){
                    tempTitle[i] = String(result2[i]);
                    console.log(""+tempTitle[i])
                }
                for(let i = 0; i < pay.length; i++){
                    temp[i] = String(result[i]).replace(/,/g, "");
                    console.log(" "+temp[i])
                    temp2[i] = parseInt(temp[i])
                    if(temp[i] != "undefined"){
                    sum += parseInt(temp2[i])*parseInt(result3[i]);
                    console.log(" "+result3[i])
                    }
                }
                productPrice = sum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
                setTotalPrice(productPrice);
                setMsg(".    "+pay.length+"   "+productPrice+'.    " " ');
                setSpeakOnce(true);
            } catch(e){
                console.log(e);
            }
        };
        fetchData();
    }, []);

    useEffect(()=>{
        switch(nextAction){
            case "comfirm_list":
                if(idx < title.length-1){
                    setSpeakOnce(false);
                    setMsg(title[idx]+"  "+ price[idx] + `  `+amount[idx]+`.    "" .`);
                    setSpeakOnce(true);
                }
                else {
                    setSpeakOnce(false);
                    setMsg(`  `+title[idx]+"  "+ price[idx] + `,  `+amount[idx]+`.    "" .`);
                    setSpeakOnce(true);
                }
                break;
            case "next_item":
                setIdx(idx+1);
                setNextAction("comfirm_list"); 
                break;

            case "first":
                setIdx(0);
                console.log("")
                history.push({pathname: `/`}); 
                break;
            default:
                break;
        }
    }, [nextAction]);

    useEffect(()=>{
        setNextAction(props.nextAction);
    }, [props.nextAction]);

    useEffect(()=>{
        console.log(nextField);
    },[nextField]);

    useEffect(()=>{
        if(speakOnce){
            props.msgInput(msg);
            btnRead();
            setSpeakOnce(false);
        }
        else{
            console.log("  ")
          }
    }, [speakOnce]);

    const btnRead = (e) => {

        speak(msg , {
            rate: 1.5,
            pitch: 1.0,
        })
    }

    function speak(text, opt_prop) {
        if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
            alert("     .")
            return
        }

        window.speechSynthesis.cancel() //   

        const prop = opt_prop || {}

        setIsSpeakDone(false);
        const speechMsg = new SpeechSynthesisUtterance()
        speechMsg.rate = prop.rate || 1 // : 0.1 ~ 10      
        speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
        speechMsg.voice = voices.filter(function(voice) { return voice.name == 'Google '; })[0];
        speechMsg.text = text

        speechMsg.onend = function(){
            setIsSpeakDone(true);
        }

        // SpeechSynthesisUtterance     
        window.speechSynthesis.speak(speechMsg)
    }

    return (
    <div>
        <Header nowPage={"PayPage"} voiceInput={props.voiceInput} msg={msg} isSpeakDone={isSpeakDone} nextActionInput={props.nextActionInput}/>
        <div className="Container">
            <PayTitle />
            {products.map((product, index) => (
                <PayInfo idx={index} num={product.num} key={product.index} info={product} voice={props.voice} nextAction={props.nextAction} />
            ))}            
            <PayUser />
            <PayDeliver />
            <PayArea price = {totalPrice} nextAction={props.nextAction}/>

        </div>
        <footer>
            <p>Footer Area</p>
        </footer>
    </div>
    );
};
export default PayPage;