import React, { useEffect, useState } from "react";
import axios from "axios";
import Breadcrumbs from "../component/Breadcrumbs";
import Header from "../component/Header";
import '../component/Container.css';
import '../component/Footer.css';
import ProductSerialArea from "../component/ProductSerialArea";
import ProductOrderArea from "../component/ProductOrderArea";
import TabMenuArea from "../component/TabMenuArea";
import MainContent from "../component/MainContent";
import { useLocation } from "react-router";

const ProductPage = (props) => {
    var voices = window.speechSynthesis.getVoices();
    const location = useLocation();
    const [info, setInfo] = useState({
        imgSrc: "",
        title: "",
        mallName: "",
        id: 0,
        price: 0,
        maker: "",
        brand: ""
    });
    const [msg, setMsg] = useState("");
    const [isSet, setIsSet] = useState(false);
    const [speakOnce, setSpeakOnce] = useState(false);
    const [isSpeakDone, setIsSpeakDone] = useState(false); //   

    //    
    const addCart = () => {
        const fetchData = async () => {
            try{
                const response = await axios.post('https://springservertest.herokuapp.com/basket/plusitem', {
                    customer: props.loginID,
                    title: info.title,
                    image: info.imgSrc,
                    price: info.price,
                    pid: info.id
                    });
                if(response.data === 1){
                    console.log("  ");
                    setMsg(`   .   "" .`);
                    setSpeakOnce(true);
                }
            } catch(e){
                console.log(e);
            }
        };
        fetchData();
    }

    //     
    useEffect(()=>{
        console.log(" : " , location.state);
        setInfo({
            imgSrc: location.state.imgSrc,
            title: location.state.title,
            mallName: location.state.mallName,
            id: location.state.id,
            price: location.state.price,
            maker: location.state.maker,
            brand: location.state.brand
        });
        setMsg(location.state.title + ".  " + location.state.price + `.   "" .`);
        setSpeakOnce(true);
    },[location.state]);

    useEffect(()=>{
        switch(props.nextAction){
            case "put_cart": // "     api 
                addCart();
                break;
            default:
                break;
        }
    }, [props.nextAction]);

    useEffect(()=>{
        if(speakOnce){
            props.msgInput(msg);
            btnRead();
            setSpeakOnce(false);
          }
          else{
            console.log("  ")
          }
    }, [speakOnce]);

    const btnRead = (e) => {
        setIsSpeakDone(false);
        speak(msg , {
            rate: 1.5,
            pitch: 1.0,
        })
    }

    function speak(text, opt_prop) {
        if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
            alert("     .")
            return
        }

        window.speechSynthesis.cancel() //   

        const prop = opt_prop || {}

        const speechMsg = new SpeechSynthesisUtterance()
        speechMsg.rate = prop.rate || 1 // : 0.1 ~ 10      
        speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
        speechMsg.voice = voices.filter(function(voice) { return voice.name == 'Google '; })[0];
        speechMsg.text = text

        speechMsg.onend = function(){
            setIsSpeakDone(true);
        }

        // SpeechSynthesisUtterance     
        window.speechSynthesis.speak(speechMsg)
    }
    return (
        <div>
        <Header nowPage={"ProductPage"} loginID={props.loginID} voiceInput={props.voiceInput} msg={msg} isSpeakDone={isSpeakDone} nextActionInput={props.nextActionInput}/>
        <div className="Container">
            <Breadcrumbs />
            <ProductSerialArea productId={info.id}/>
            <ProductOrderArea productInfo={info} nextAction={props.nextAction}/>
            <TabMenuArea />
            <MainContent />
        </div>
        <footer>
            <p>Footer Area</p>
        </footer>
    </div>
    );
};

export default ProductPage; 

