import React ,{ useEffect, useState } from 'react';
import { useHistory } from "react-router";
import axios from "axios";
import Header from '../component/Header';
import '../component/Container.css';
import '../component/Footer.css';
import JoinArea from '../component/JoinArea';

const JoinPage = (props) => {
    const history= useHistory();
    var voices = window.speechSynthesis.getVoices();
    const [joinId, setJoinId] = useState("");
    const [joinPwd, setJoinPwd] = useState("");
    const [joinName, setJoinName] = useState("");
    const [joinAddress, setJoinAddress] = useState("");
    const [joinCardComp, setJoinCardComp] = useState(null);
    const [joinCardNum, setJoinCardNum] = useState(null);
    const [joinForm, setJoinForm] = useState({
        id: "",
        pwd: "",
        name: "",
        address: "",
        card_company: null,
        card_num: null
    })
    const [msg, setMsg] = useState(" .     ,     .   . ");
    const [isSpeakDone, setIsSpeakDone] = useState(false); //   
    const [speakOnce, setSpeakOnce] = useState(true);
    const [fieldValue, setFieldValue] = useState(""); //  raw 
    const [nextField, setNextField] = useState("id"); //     
    const [nextAction, setNextAction] = useState("");
    const [joinSuccess, setJoinSuccess] = useState(false);

    const handleFormInput = (input) => {
        setJoinForm({
            id: input.id,                                                                                                                                                                                                                                                                                                                                                                                                                       
            pwd: input.pwd
        });
    }

    const handleJoinFieldValueInput = (input) => {
        setFieldValue(input);
    }

    const checkID = (input) => {
        const fetchData = async () => {
            try {
                const response = await axios.get('https://springservertest.herokuapp.com/register/check',{
                    params:{
                        checkid: input
                    }
                });
                console.log("  : "+response.data);
                if(response.data === -1){
                    setMsg("  .    .");
                    setNextField("id");
                    setSpeakOnce(true);
                }
                else if(response.data === 1){
                    setMsg("  .   .");
                    setNextField("pwd");
                    setSpeakOnce(true);
                }
            } catch (error) {
                console.log(error);
            }
        };
        fetchData();
    }

    const callJoinApi = () => {
        const fetchData = async () => {
            try{
                const response = await axios.post('https://springservertest.herokuapp.com/register', {
                    id: joinId,
                    pwd: joinPwd,
                    name: joinName,
                    address: joinAddress
                    });
                console.log(response.data);
                const result = response.data;
                if(result === 1){
                    await axios.post('https://springservertest.herokuapp.com/card/pluscard', {
                        userid: joinId,
                        card_company: joinCardComp,
                        card_num: joinCardNum
                    });
                    setMsg(" .  .");
                    setJoinSuccess(true);
                    setSpeakOnce(true);
                }
                else if(result === -1){
                    setMsg("     .  .");
                    setJoinSuccess(true);
                    setSpeakOnce(true);
                }
            } catch(e){
                console.log(e);
            }
        };
        fetchData();
    }

    const convertToJson = (input) => {
        let jsonData = JSON.stringify(input);
        console.log(jsonData);
    }

    const convertStr = (input) => {
        var resultStr = "";
        var strLen = input.length;

        for(var i=0; i<strLen; i++){
            var tmp = input.substr(i,1);
            if(tmp == ';') resultStr += tmp;
            else resultStr += (tmp + ';');
        }
        console.log(resultStr);
        return resultStr;
    }

    // useEffect(()=>{
    //     console.log(" : " + joinForm.id);
    //     console.log(" : " + joinForm.pwd);
    // }, [joinForm]);

    useEffect(()=>{
        if(fieldValue !== ""){
            switch(nextField){
                case "id":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            checkID(joinId);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setMsg("  .");
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempID = (props.voice).replace(/(\s*)/g, "").toLowerCase();
                            var tempID2 = convertStr(tempID);
                            console.log(": " +tempID);
                            setJoinId(tempID);
                            setMsg(`  "`+tempID2+`" .  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "pwd":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg(" .");
                            setNextField("name");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setMsg("  .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempPWD = (props.voice).replace(/(\s*)/g, "").toLowerCase();
                            var tempPWD2 = convertStr(tempPWD);
                            console.log(": " +tempPWD);
                            setJoinPwd(tempPWD);
                            setMsg("  "+tempPWD2+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "name":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("  .     .   .");
                            setNextField("address");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setMsg(" .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            console.log(": " +props.voice);
                            setJoinName(props.voice);
                            setMsg("  "+props.voice+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "address":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg(`    .       .  "" .   .`);
                            setNextField("card_company");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setMsg("  .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempAddr = convertStr(props.voice);
                            console.log(": " +tempAddr);
                            setJoinAddress(props.voice);
                            setMsg("    .   "+tempAddr+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                            
                        default:
                            break;
                    }
                    break;
                case "card_company":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("  16   4 .");
                            setNextField("card_num1");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setMsg(" .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            console.log(": " +props.voice);
                            setJoinCardComp(props.voice);
                            setMsg("  "+props.voice+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        case "skip":
                            setSpeakOnce(false);
                            setMsg("   .");
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "card_num1":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("  16   4 .");
                            setNextField("card_num2");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            setJoinCardNum(null);
                            setMsg("  16   4 .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempNum1 = convertStr(props.voice);
                            console.log(" 1: " + props.voice);
                            setJoinCardNum(props.voice);
                            setMsg(`  4  `+tempNum1+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "card_num2":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("  16   4 .");
                            setNextField("card_num3");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            var num1 = joinCardNum.substr(0,4);
                            setJoinCardNum(num1);
                            setMsg("  16   4 .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempNum2 = convertStr(props.voice);
                            console.log(" 2: " + props.voice);
                            setJoinCardNum(joinCardNum + props.voice);
                            setMsg(`  4  `+tempNum2+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "card_num3":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("  16   4 .");
                            setNextField("card_num4");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            var num2 = joinCardNum.substr(0,8);
                            setJoinCardNum(num2);
                            setMsg("  16   4 .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempNum3 = convertStr(props.voice);
                            console.log(" 3: "+props.voice);
                            setJoinCardNum(joinCardNum+props.voice);
                            setMsg(`  4  `+tempNum3+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "card_num4":
                    switch(nextAction){
                        case "next":
                            setSpeakOnce(false);
                            setMsg("   .");
                            callJoinApi();
                            setNextField("done");
                            setSpeakOnce(true);
                            break;
                        case "again":
                            setSpeakOnce(false);
                            var num3 = joinCardNum.substr(0,12);
                            setJoinCardNum(num3);
                            setMsg("  16   4 .")
                            setSpeakOnce(true);
                            break;
                        case "normal":
                            setSpeakOnce(false);
                            var tempNum4 = convertStr(props.voice);
                            console.log(" 4: "+props.voice);
                            setJoinCardNum(joinCardNum+props.voice);
                            setMsg(`  4  `+tempNum4+`.  "",   "" .`);
                            setSpeakOnce(true);
                            break;
                        default:
                            break;
                    }
                    break;
                case "done":
                    break;
                default:
                    break;
            }    
        }
        // setCnt(cnt+1);
    }, [fieldValue, nextAction]);

    useEffect(()=>{
        setNextAction(props.nextAction);
    }, [props.nextAction]);

    useEffect(()=>{
        console.log(nextField);
    },[nextField]);

    useEffect(()=>{
        console.log("joinId  : " + joinId);
    }, [joinId]);
    useEffect(()=>{
        console.log("joinPwd  : " + joinPwd);
    }, [joinPwd]);
    useEffect(()=>{
        console.log("joinName  : " + joinName);
    }, [joinName]);
    useEffect(()=>{
        console.log("joinAddress  : " + joinAddress);
    }, [joinAddress]);
    useEffect(()=>{
        console.log("joinCardComp  : " + joinCardComp);
    }, [joinCardComp]);
    useEffect(()=>{
        console.log("joinCardNum  : " + joinCardNum);
    }, [joinCardNum]);

    useEffect(()=>{
        if(speakOnce){
            props.msgInput(msg);
            btnRead();
            setSpeakOnce(false);
          }
          else{
            console.log("  ")
          }
    }, [speakOnce]);


    const btnRead = (e) => {
        setIsSpeakDone(false);
        speak(msg , {
            rate: 1.0,
            pitch: 1.0,
        })
    }

    function speak(text, opt_prop) {
        if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
            alert("     .")
            return
        }

        window.speechSynthesis.cancel() //   

        const prop = opt_prop || {}

        const speechMsg = new SpeechSynthesisUtterance()
        speechMsg.rate = prop.rate || 1 // : 0.1 ~ 10      
        speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
        speechMsg.voice = voices.filter(function(voice) { return voice.name == 'Google '; })[0];
        speechMsg.text = text

        speechMsg.onend = function(){
            if(joinSuccess) history.push({pathname: `/`});
            setIsSpeakDone(true);
        }

        // SpeechSynthesisUtterance     
        window.speechSynthesis.speak(speechMsg)
    }

    return (
        <div>
            <Header nowPage={"JoinPage"} loginID={props.loginID} voiceInput={props.voiceInput} nextActionInput={props.nextActionInput} isSpeakDone={isSpeakDone} joinFieldValueInput={handleJoinFieldValueInput}/>
            <div className="Container">
                <JoinArea joinFormInput={handleFormInput} id={joinId}/>
            </div>
            <footer>
                <p>Footer Area</p>
            </footer>
        </div>
    );
};

export default JoinPage; 