import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router';
import Breadcrumbs from '../component/Breadcrumbs';
import ProductGrid from '../component/ProductGrid';
import SearchFilter from '../component/SearchFilter';
import SearchResult from '../component/SearchResult';
import '../index.css';
import '../component/Container.css';
import Header from '../component/Header';


const SearchPage = (props) => {
  var voices = window.speechSynthesis.getVoices();
  const query = useParams().keyword;
  const [searchOpt, setSearchOpt] = useState("sim");
  const [searchOptKR, setSearchOptKR] = useState("");
  const [isLoad, setIsLoad] = useState("false"); //   true   flag.
  const [nextAction, setNextAction] = useState("");
  const [targetIdx, setTargetIdx] = useState(0);
  const [productInfo, setProductInfo] = useState({
    set: 0,
    idx: 0,
    title: "unknown",
    price: 0
  });
  const [msg, setMsg] = useState("");
  const [isSpeakDone, setIsSpeakDone] = useState(false); //   
  const [speakOnce, setSpeakOnce] = useState(false);

  const handleSetProductInfo = (input) => {
    setProductInfo({
      set: input.set,
      idx: input.idx,
      title: input.title,
      price: input.price
    });
  }

  const handleIsLoadInput = (input) => {
    if(input === true){
      setMsg(" " + query+ "  " + searchOptKR +  `   .   ,       .`);
      setSpeakOnce(true);
    }
  }

  const nextItem = () => {
    var idx = targetIdx;
    setTargetIdx(idx+1);
  }

  const prevItem = () => {
    var idx = targetIdx;
    setTargetIdx(idx-1);
  }

const btnRead = () => {
  setIsSpeakDone(false);
  speak(msg, {
      rate: 1.5,
      pitch: 1.0,
  })
}

function speak(text, opt_prop) {
    if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
        return
    }

    // window.speechSynthesis.cancel() //   

    const prop = opt_prop || {}

    const speechMsg = new SpeechSynthesisUtterance()
    speechMsg.rate = prop.rate || 1.2 // : 0.1 ~ 10      
    speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
    speechMsg.voice = voices.filter(function(voice) { return voice.name === 'Google '; })[0];
    speechMsg.text = text

    speechMsg.onend = function(){
      setIsSpeakDone(true);
    }

    // SpeechSynthesisUtterance     
    window.speechSynthesis.speak(speechMsg)
}

useEffect(()=>{
  if(speakOnce){
    props.msgInput(msg);
    btnRead();
    setSpeakOnce(false);
  }
  else{
    console.log("  ")
  }
}, [speakOnce]);

useEffect(()=>{
  console.log("next Action parts");
  switch(nextAction){
    case "select_reading_items":
      setMsg(`  ` + productInfo.title +`, ` + productInfo.price + ` .      " ",     "",    "" .`);
      setSpeakOnce(true);
      break;
    case "next_item":
      if(targetIdx === 26){
        setMsg(`  .     "",      " " .`);
        setSpeakOnce(true);
      }
      else {
        nextItem();
        setTimeout(()=>setNextAction("select_reading_items"), 1000);
      }
      break;
    case "prev_item":
      if(targetIdx === 0){
        setMsg(`  .     "",      " " .`);
        setSpeakOnce(true);
      }
      else {
        prevItem();
        setTimeout(()=>setNextAction("select_reading_items"), 1000);
      }
      break;
    case "change_search_option":
      setMsg(`    "", "", "", "" .   .`);
      setSpeakOnce(true);
      break;
    case "set_search_option_sim":
      setSearchOpt("sim");
      setSearchOptKR("");
      setSpeakOnce(true);
      break;
    case "set_search_option_date":
      setSearchOpt("date");
      setSearchOptKR("");
      setSpeakOnce(true);
      break;
    case "set_search_option_asc":
      setSearchOpt("asc");
      setSearchOptKR("");
      setSpeakOnce(true);
      break;
    case "set_search_option_dsc":
      setSearchOpt("dsc");
      setSearchOptKR("");
      setSpeakOnce(true);
      break;
    default:
      break;
  }
  setIsLoad(false);
}, [nextAction]);

useEffect(()=>{
  if(isLoad){ 
    setNextAction(props.nextAction);
  }
}, [props.nextAction, isLoad]);

useEffect(()=>{
  console.log("nextAction  : "+nextAction);
}, [nextAction]);
useEffect(()=>{
  console.log("productInfo  : ");
}, [productInfo]);
useEffect(()=>{
  console.log("isLoad  : "+isLoad);
}, [isLoad]);

useEffect(()=>{
  console.log("targetIdx  : "+ targetIdx);
}, [targetIdx]);

  return (
    <div>
      <Header 
        nowPage={"SearchPage"} 
        loginID={props.loginID} 
        voiceInput={props.voiceInput} 
        msg={msg} 
        isSpeakDone={isSpeakDone} 
        nextActionInput={props.nextActionInput} 
        setLoad={setIsLoad}
      />
      <div className="Container">
        <Breadcrumbs />
        <SearchFilter option={searchOptKR}/>
        <SearchResult />
        <ProductGrid 
          setProduct={handleSetProductInfo}
          isLoadInput={handleIsLoadInput}
          targetIdx={targetIdx} 
          keyword={useParams().keyword} 
          option={searchOpt} 
          voice={props.voice} 
          voiceInput={props.voiceInput} 
          nextAction={props.nextAction}
        />
      </div>
      <footer>
          <p>Footer Area</p>
      </footer>
    </div>
  );
};


export default SearchPage;