import React, { useState, useEffect } from "react";
import '../index.css';
import '../css/Mainpage.css'
import Logo from '../component/Logo';
import SearchBar from '../component/SearchBar';
import { useHistory } from "react-router";

const MainPage = (props) => {
  const history= useHistory();
  const voices = window.speechSynthesis.getVoices();
  const [msg, setMsg] = useState("");
  const [speakOnce, setSpeakOnce] = useState(false);
  const [isSpeakDone, setIsSpeakDone] = useState(false); //   
  const [isLogin, setIsLogin] = useState(false);
  const [nextAction, setNextAction] = useState("");

  function btnRead() {
    setIsSpeakDone(false);
    speak( msg, {
        rate: 1.5,
        pitch: 1.0,
        // lang: "ko-KR"
    })
  }

  function speak(text, opt_prop) {
      if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
          alert("     .")
          return
      }

      window.speechSynthesis.cancel() //   

      const prop = opt_prop || {}

      const speechMsg = new SpeechSynthesisUtterance()
      speechMsg.rate = prop.rate || 1.3 // : 0.1 ~ 10      
      speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
      // speechMsg.lang = prop.lang
      speechMsg.voice = voices.filter(function(voice) { return voice.name === 'Google '; })[0];
      speechMsg.text = text

      speechMsg.onend = function(){
        setIsSpeakDone(true);
      }

      // SpeechSynthesisUtterance     
      window.speechSynthesis.speak(speechMsg)
  }

  useEffect(()=>{
    if(props.loginID == null){
      setSpeakOnce(false);
      setMsg(`   .    .       .    "",  "" .`);
      setSpeakOnce(true);
    }
    else{
      setSpeakOnce(false);
      setIsLogin(true);
      setMsg(props.loginID+`,    .   , "", " "    .`);
      setSpeakOnce(true);
    }
  },[]);

  useEffect(()=>{
    switch(props.nextAction){
      case "login_first":
        setSpeakOnce(false);
        setMsg(`  .    "",  "" .`);
        setSpeakOnce(true);
        break;
      default:
        break;
    }
  }, [props.nextAction]);


  useEffect(()=>{
  }, [props.loginID, isLogin]);

  useEffect(()=>{
    if(speakOnce){
      props.voiceInput("");
      props.msgInput(msg);
      btnRead();
      setSpeakOnce(false);
    }
    else{
      console.log("  ")
    }
  }, [speakOnce]);

  useEffect(()=>{
  }, [msg, props.nextAction]);

  // useEffect(()=>{

  // }, [props.nextAction]);

  return (
    <div className="main_page">
      {props.nextAction === "join_member" && history.push({pathname: `/join`})}
      {props.nextAction === "login" && history.push({pathname: `/login`})}
      {props.nextAction === "go_cart" && history.push({pathname: `/cart`})}
      {props.nextAction === "go_pay" && history.push({pathname: `/pay`})}

      <div className="main_logo" ><Logo /></div>
        <SearchBar 
          nowPage={"MainPage"} 
          voiceInput ={props.voiceInput} 
          isSpeakDone={isSpeakDone} 
          nextActionInput={props.nextActionInput}
          searchFlag={isLogin}
          />
    </div>
  );
};

export default MainPage; 