import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import { useSpeechRecognition } from "react-speech-kit";
import "./SearchBar.css";
import mic from "../img/voice_mic.png";

const SearchBar = (props) => {
  const history = useHistory();

  //   url   state
  const [url, setUrl] = useState("/search/");

  //  string    state
  const [value, setValue] = useState("");

  //     state
  const [isVoiceDone, setIsVoiceDone] = useState(false);

  //   
  const [nowPage, setNowPage] = useState(props.nowPage);

  //  on/off  
  const [searchFlag, setSearchFlag] = useState(false);

    // 
    const onChange = (e) => {
      console.log(e.target.value);
      setValue(e.target.value)		//  value {text} 
    }

  const { listen, listening, stop } = useSpeechRecognition({
    onResult: (result) => {
      if(result == ""){}
      else{
      //     
      // console.log("onResult ");
      setIsVoiceDone(false);
      if(props.nowPage === "LoginPage" || props.nowPage === "JoinPage"|| props.nowPage === "CartPage")
        result = convertVoice(result);
      setValue(result);
      props.voiceInput(result); //     app.js state  .
      setIsVoiceDone(true);
      // console.log("onResult ");
      }
    },
    onEnd: () => {
      //       
      var voiceValue = value;
      setValue(""); //  
      switch (nowPage){ //    
        case "MainPage":
          switch(voiceValue){
            case "": case " ":
              setNowPage("JoinPage");
              props.nextActionInput("join_member");
              break;
            case "":
              props.nextActionInput("login");
              break;
            case "":
              props.nextActionInput("go_cart");
              break;
            case "": case " ":
              props.nextActionInput("go_pay");
              break;
            default:
              //        
              if(searchFlag){
                props.nextActionInput("search_by_keyword");
                history.push({ pathname: url });
                break;
              }
              else{
                props.nextActionInput("login_first");
                break;
              }
          }
          break;
        case "LoginPage": //
          props.loginFieldValueInput("");
          props.loginFieldValueInput(voiceValue); //     .
          break;
        case "JoinPage": // 
          switch(voiceValue){
            case "":
              props.nextActionInput("next");
              break;
            case "": case " ":
              props.nextActionInput("again");
              break;
            case "":
              props.nextActionInput("skip");
              break;
            default:
              props.nextActionInput("normal");
              props.joinFieldValueInput(voiceValue); //     .
              break;
          }
          break;
        case "SearchPage":
          switch(voiceValue){
            case "": case " ":
              props.nextActionInput("change_search_option");
              break;
            case "": case " ":
              props.nextActionInput("set_search_option_sim");
              break;
            case "": case " ":
              props.nextActionInput("set_search_option_date");
              break;
            case "":  case " ": case " ": case "  ":
              props.nextActionInput("set_search_option_asc");
              break;
            case "": case " ": case " ": case "  ":
              props.nextActionInput("set_search_option_dsc");
              break;
            case "": case " ":
              props.nextActionInput("select_reading_items");
              break;
            case "": case " ":
              props.nextActionInput("detail_info");
              break;
            case "":
              props.nextActionInput("next_item");
              break;
            case "":
              props.nextActionInput("prev_item");
              break;
            default:
              break;
          }
          props.setLoad(true);
          break;
        case "ProductPage":
          switch(voiceValue){
            case "":
              props.nextActionInput("go_cart");
              break;
            case "":
              props.nextActionInput("put_cart");
              break;
            default:
              break;
          }
          break;
        case "CartPage":
          switch(voiceValue){
            case "":
              props.nextActionInput("yes");
              break;
            case "": case "":
              props.nextActionInput("no");
              break;
            case "":
              props.nextActionInput("add");
              break;
            case "":
              props.nextActionInput("next_item");
              break; 
            case "":
              props.nextActionInput("next");
              break;   
            case "":
              props.nextActionInput("skip");
              break;         
            case "": case " ": case "": case " ":
              props.nextActionInput("comfirm_pay");
              break;
            case " ": case " ":
              props.nextActionInput("register_card");
              break;
            case " ":  case "":
              props.nextActionInput("comfirm_card");
              break;
            case " ": case "":
              props.nextActionInput("product_info");
              break;
            case " ":
              props.nextActionInput("delete_all");
              break;
            case "": case " ":
              props.nextActionInput("again");
              break;
            default:
              props.nextActionInput("normal");
              props.cartFieldValueInput(voiceValue); //     .
              break;
          }
          break;
        case "PayPage":
          switch(voiceValue){
            case " ": case "":
              props.nextActionInput("comfirm_list");
              break;
            case "":
              props.nextActionInput("first");
              break;
            case "": 
              props.nextActionInput("next_item");
              break;
            default:
              break;
          }
          break;
        default:
          break;
      }
    },
  });

  const convertVoice = (input) => {
    switch(input){
      case "":
        return "#";
      case "":
        return "@";
      case "":
        return "!";
      case "":
        return "$";
      case "":
        return "*";
      case "": case "":
        return "1";
      case "":
        return "2";
      case "": case "":
        return "3";
      case "":
        return "4";
      case "":
        return "5";
      case "":
        return "6";
      case "":
        return "7";
      case "":
        return "8";
      case "":
        return "9";
      case "": case "":
        return "0";
      case "":
        if(props.nowPage === "CartPage") return "2";
        if(props.nowPage === "JoinPage" || props.nowPage === "LoginPage") return "e";
      case "": case "":
        return "t";
      default:
        return input;
    };
  }
  useEffect(() => {
    // console.log(": " + value);
    updateURL(value);
  }, [value, props.voiceInput]);

  //      .
  useEffect(() => {
    // app.js  state.        true .
    if (props.isSpeakDone) {
      console.log(" ");
      props.voiceInput(" ");
      listen();
    }
  }, [props.isSpeakDone]);

 //      
 useEffect(() => {
  if (isVoiceDone  && (value !== "")) {//    ,      
    var timer = 3; 
    var counter = setInterval(function(){
      if(isVoiceDone){
        if(timer === 0){
          console.log("   3 .");
          stop(); //    
          setIsVoiceDone(false);
          clearInterval(counter);
        }
        timer--;
      }
      else {}
    }, 1000);

    // setTimeout(function () {
    //   // 3  .
      
    // }, 3000);
  }
}, [isVoiceDone]);

useEffect(()=>{
  console.log("  :" + value);
}, [value]);

useEffect(()=>{
  setSearchFlag(props.searchFlag);
}, [props.searchFlag]);

useEffect(()=>{
  console.log("SearchFlag: "+searchFlag);
}, [searchFlag]);

  const updateURL = (query) => {
    var newURL;
    if (query === `""`) {
      return;
    }
    newURL = "/search/" + query;

    setUrl(newURL);
  };

  return (
    <form className="SearchBar">
      <button className="mic_button">
        <img src={mic} alt="mic" onMouseEnter={()=> stop()} />
      </button>
      <input type="text" onChange={onChange}/>
      {/* <Link to={url}> */}
        <button className="search"></button>
      {/* </Link> */}
    </form>
  );
};

export default SearchBar;