import React ,{ useEffect, useState } from 'react';
import { useHistory } from "react-router";
import axios from "axios";
import Header from '../component/Header';
import '../component/Container.css';
import '../component/Footer.css';
import LoginArea from '../component/LoginArea';

const LoginPage = (props) => {
    const history = useHistory();
    var voices = window.speechSynthesis.getVoices();
    const [msg, setMsg] = useState(" .     ,     .  . ");
    const [id, setId] = useState("");
    const [pwdLength, setPwdLength] = useState(0);
    const [pwdCheckCnt, setPwdCheckCnt] = useState(3); //     count
    const [rndIdx, setRndIdx] = useState({first: -1, second: -1}); //    
    const [isSpeakDone, setIsSpeakDone] = useState(false); //   
    const [speakOnce, setSpeakOnce] = useState(true);
    const [fieldValue, setFieldValue] = useState(""); //      
    const [nextField, setNextField] = useState("id"); //     
    const [loginSuccess, setLoginSuccess] = useState(false);  //   

    const getPwdLength = (input) => {
        const fetchData = async () => {
            try{
                const response = await axios.get('https://springservertest.herokuapp.com/login/length', {
                    params: {
                        userid: input
                    }});
                console.log(response.data);
                setPwdLength(response.data);
                speakPwdChecking(response.data, true, 0);
            } catch(e){
                console.log(e);
            }
        };
        fetchData();
    }

    //  2   
    const checkPwd = (idx1, idx2, input) => {
        const fetchData = async () => {

            //   , ,   2  
            var str = input.replace(/(\s*)/g, "").toLowerCase();
            var ch1 = str.substr(0,1);
            var ch2 = str.substr(1,1);

            try{
                //  
                const response1 = await axios.get('https://springservertest.herokuapp.com/login/check', {
                    params: {
                        userid: id,
                        index: idx1,
                        input: ch1
                    }});
                    console.log("1   : "+response1.data);
                if(response1.data === 1){ //     
                    const response2 = await axios.get('https://springservertest.herokuapp.com/login/check', {
                    params: {
                        userid: id,
                        index: idx2,
                        input: ch2
                    }});
                    console.log("2   : "+response2.data);
                    if(response2.data === 1){ //    1    
                        if(pwdCheckCnt-1 === 0){ //   
                            setSpeakOnce(false);
                            setMsg(" .   .");
                            console.log(" ");
                            setLoginSuccess(true);
                            setSpeakOnce(true);
                        }
                        else{
                            setPwdCheckCnt(pwdCheckCnt-1);
                            speakPwdChecking(pwdLength, true, -1, -1);
                        }
                    }
                    else{ //       
                        speakPwdChecking(pwdLength, false, idx1, idx2);
                    }
                }
                else{ //        
                    speakPwdChecking(pwdLength, false, idx1, idx2);
                }
            } catch(e){
                console.log(e);
            }
        };
        fetchData();
    }

    const rand = (length, checkIdx) =>{
        var idx;
        do{
            idx = Math.floor(Math.random()*length) + 1;
        }
        while (idx == checkIdx)
        return idx;
    }

    //        
    const speakPwdChecking = (pwdLength, isPass, falseIdx1, falseIdx2) => {
        if(pwdLength === -1){ //     
            setSpeakOnce(false);
            setMsg("   .  .");
            setNextField("id"); //id  
            setSpeakOnce(true);
        }
        else{
            if(pwdCheckCnt > 0){ //  0 
                setSpeakOnce(false);
                //  2 
                var rndNum1 = rand(pwdLength, -1);
                var rndNum2 = rand(pwdLength, rndNum1);
                if(isPass){ //     ->    
                    setMsg(" "+rndNum1+"  " + rndNum2+"   .");
                    setRndIdx({first: rndNum1, second: rndNum2});
                } 
                else{ //     ->    
                    setMsg("  .  "+falseIdx1+"  " + falseIdx2+"   .");
                    setRndIdx({first: falseIdx1, second: falseIdx2});
                } 
                setNextField("pwd") //    
                setSpeakOnce(true);
            }
        }
    };

    useEffect(()=>{
        if(fieldValue !== ""){
            switch(nextField){
                case "id": // 
                    setSpeakOnce(false);
                    var tempID = (fieldValue).replace(/(\s*)/g, "").toLowerCase();
                    console.log(": " + tempID);
                    setId(tempID);
                    getPwdLength(tempID);
                    break;
                case "pwd": // 
                    checkPwd(rndIdx.first, rndIdx.second, fieldValue);
                    break;
                case "done": //  
                    break;
                default:
                    break;
            }    
        }
        // setCnt(cnt+1);
    }, [fieldValue]);

    //3     
    // useEffect(()=>{
    //     if(pwdCheckCnt === 0){
    //         setSpeakOnce(false);
    //         setMsg(" .   .");
    //         props.loginIDInput(id);
    //         console.log(" ");
    //         setLoginSuccess(true);
    //         setSpeakOnce(true);
    //     }
    // }, [pwdCheckCnt]);
    
    useEffect(()=>{
        if(speakOnce){
            props.msgInput(msg);
            btnRead();
            setSpeakOnce(false);
          }
          else{
            console.log("  ")
          }
    }, [speakOnce]);

    useEffect(()=>{
        console.log("pwdLength  : "+pwdLength);
    }, [pwdLength]);
    useEffect(()=>{
        console.log("nextField  : "+nextField);
    }, [nextField]);
    useEffect(()=>{
        console.log("rndIdx  : "+rndIdx);
    }, [rndIdx]);
    useEffect(()=>{
        console.log("pwdCheckCnt  : "+pwdCheckCnt);
    }, [pwdCheckCnt]);
    useEffect(()=>{
        console.log("isSpeakDone  : "+isSpeakDone);
    }, [isSpeakDone]);
    useEffect(()=>{
        console.log("loginSuccess  : "+loginSuccess);
    }, [loginSuccess]);

    const btnRead = (e) => {
        setIsSpeakDone(false);
        speak(msg , {
            rate: 1.1,
            pitch: 1.0,
        })
    }

    function speak(text, opt_prop) {
        if (typeof SpeechSynthesisUtterance === "undefined" || typeof window.speechSynthesis === "undefined") {
            alert("     .")
            return
        }

        window.speechSynthesis.cancel() //   

        const prop = opt_prop || {}
        const speechMsg = new SpeechSynthesisUtterance()
        speechMsg.rate = prop.rate || 1.1 // : 0.1 ~ 10      
        speechMsg.pitch = prop.pitch || 1 // : 0 ~ 2
        speechMsg.voice = voices.filter(function(voice) { return voice.name == 'Google '; })[0];
        speechMsg.text = text

        speechMsg.onend = function(){
            if(loginSuccess){
                props.loginIDInput(id);
                props.nextActionInput("");
                history.push({pathname: "/"});
            }
            else{
                setIsSpeakDone(true);
            }
        }

        // SpeechSynthesisUtterance     
        window.speechSynthesis.speak(speechMsg)
    }

    return (
        <div>
            <Header 
                nowPage={"LoginPage"} 
                loginID={props.loginID} 
                voiceInput={props.voiceInput} 
                isSpeakDone={isSpeakDone} 
                loginFieldValueInput={setFieldValue}
                />
            <div className="Container">
                <LoginArea />
            </div>
            <footer>
                <p>Footer Area</p>
            </footer>
        </div>
    );
};

export default LoginPage; 